import Requirement from "@/Requirement"
import Deck from "@/cards/Deck"
import DialogueTree from "@/DialogueTree"
import GameStage from "@/stages/GameStage"

export default class Ending {
  public id: string = ""
  public title: string = ""
  public likelihood: number = 0
  private requirements: Requirement[][] = []

  private dialogue: any

  load(state: any) {
    this.id = state.id
    this.title = state.title
    this.likelihood = (state.likelihood) ? state.likelihood : 1000
    this.requirements = Ending.parseRequirements(state.requirements)
    this.dialogue = (state.dialogue) ? state.dialogue : undefined
  }

  createDialogueTree(stage: GameStage): DialogueTree {
    return DialogueTree.parseDialogue(this.dialogue, stage)
  }

  isFulfilled(deck: Deck) {
    try {
      for (let ors of this.requirements) {
        let fulfilled = Ending.orGroupFulfilled(deck, ors)
        if (fulfilled) return true
      }
    } catch (e) {
      console.error(`Could calculate fulfilment for ending "${this.id}".\n${e.message}`)
    }
    return false
  }

  intensityScore(deck: Deck) {
    let sum = 0
    try {
      for (let orGroup of this.requirements) {
        //console.log(req.toString(), req.intensityScore(deck))
        if (Ending.orGroupFulfilled(deck, orGroup)) {
          for (let and of orGroup) {
            sum += and.intensityScore(deck)
          }
        }
      }
    } catch (e) {
      console.error(`Could not calculate intensity score for ending "${this.id}".\n${e.message}`)
    }

    return sum
  }

  private static orGroupFulfilled(deck: Deck, orGroup: Requirement[]): boolean {
    let fulfilled = true
    for (let req of orGroup) {
      if (!req.isFulfilled(deck)) {
        fulfilled = false
        break
      }
    }
    return fulfilled
  }

  private static parseRequirements(reqs: string) {
    let requirements = []

    // Normalize: remove lowercase any newlines
    reqs = reqs.toLowerCase().replace(/(\r\n|\n|\r)/gm, " ")
    // Create list where each entry is an OR term. Each OR term is a list of AND terms. Each AND term is a string.
    let ors = reqs.split(/\bor\b/).map(or => or.split(/\band\b/))
    for (let ands of ors) {
      let mode = "or"
      for (let [i, exp] of ands.entries()) {
        if (i > 0) mode = "and"

        exp = exp.trim()
        let operator = this.getOperator(exp)
        let left = this.getLeft(exp, operator)
        let right = this.getRight(exp, operator)

        let [fun, args] = this.stripFun(left)
        args = args.map((arg: string) => arg.replaceAll("\"", ""))
        //console.log(`\t[${fun}]: [${args}] [${operator}] [${right}]`)
        requirements.push(new Requirement(mode, args, operator, Number.parseInt(right), Requirement.functions(fun)))
      }
    }

    return this.groupRequirements(requirements)
  }

  private static groupRequirements(reqs: Requirement[]) {
    let requirements: Requirement[][] = []
    let group = []
    for (let req of reqs) {
      if (req.mode == "or" && group.length > 0) {
        requirements.push(group)
        group = []
      }
      group.push(req)
    }
    requirements.push(group)

    return requirements
  }

  private static stripFun(exp: string): any[] {
    let funMatch = exp.match(/.*(?=\()/)
    let fun
    if (funMatch != null) fun = funMatch[0]
    else fun = ""

    let args: string[] = [exp]
    if (fun != "") {
      let expMatch = exp.match(/(?<=\().*(?=\))/)
      if (expMatch != null) args = this.splitArgs(expMatch[0])
      else console.error(`Bad expression: ${exp}`)
    }

    return [fun, args]
  }

  private static splitArgs(args: string): string[] {
    return args.split(",").map(arg => arg.trim())
  }

  private static getLeft(exp: string, operator: string): string {
    return exp.split(operator)[0]?.trim()
  }

  private static getRight(exp: string, operator: string): string {
    let e = exp.split(operator)[1]?.trim()
    return e || "0"
  }

  private static getOperator(exp: string): string {
    let operatorMatch = exp.match(/ > | >= | == | <= | < /)
    let operator
    if (operatorMatch == null) operator = ">"
    else operator = operatorMatch[0].trim()
    return operator

  }

}
