import { reactive, toRefs } from 'vue'
import util from "@/util"
import axios from "axios"
import MarkdownIt from 'markdown-it'

async function fetchList<E>(pathList: string[]): Promise<E[]> {
  let promises: any[] = []
  for (let path of pathList) {
    promises.push(util.fetchHjson(path))
  }
  let objects: E[] = await Promise.all(promises)
  return objects
}

const state = reactive({
  loading: true,
  cardFamilyString: "" as string,
  dialogueString: "" as string,
  dialogue2String: "" as string,
  endingsString: "" as string,
  artistsString: "" as string,
  patreonCredits: "",
  patchNotesString: ""
})

export default function globalState() {
  const load = async () => {
    state.loading = true

    // Await all resources in parallel
    let [familyList, dialogue, dialogue2, endingsList, artists, credits, patchList] = await Promise.all([
      util.fetchHjson('cards/series.hjson'),
      util.fetchHjson('dialogue.hjson'),
      util.fetchHjson('dialogue-2.hjson'),
      util.fetchHjson('endings/endings.hjson'),
      util.fetchHjson('cards/artists.hjson'),
      axios.get('https://www.blushingdefeat.com/assets/blushing-defeat-credits.json'),
      util.fetchHjson('patch-notes/content.hjson')
    ])

    state.dialogueString = JSON.stringify(dialogue)
    state.dialogue2String = JSON.stringify(dialogue2)
    state.artistsString = JSON.stringify(artists)
    state.patreonCredits = credits.data

    // Second round of await for resources specified in the first await
    let [families, patches, endings] = await Promise.all([
      fetchList(familyList.map((path: string) => `cards/${path}`)),
      fetchList(patchList.map((patchId: string) => `patch-notes/${patchId}.hjson`)),
      fetchList(endingsList.map((ending: any) => `endings/${ending.id}.hjson`))
    ])

    // Load card families
    state.cardFamilyString = JSON.stringify(families)

    // Load patch notes
    let md = new MarkdownIt()
    for (let [i, patchId] of patchList.entries()) {
      let patch: any = patches[i]
      patch.id = patchId
      patch.text = md.render(patch.text)
    }
    state.patchNotesString = JSON.stringify(patches)

    // Load endings
    for (let ending of endingsList) {
      let filtered = endings.filter((e: any) => e.id == ending.id)
      if (filtered.length != 1) {
        console.error(`Could not load ending "${ending.id}".`)
        continue
      }
      let dialogue = filtered[0]
      ending.dialogue = dialogue
    }
    state.endingsString = JSON.stringify(endingsList)

    state.loading = false
  }

  return { ...toRefs(state), load }
}
