import Requirement from "@/Requirement"
import Deck from "@/cards/Deck"
import Ending from "@/Ending"
import globalState from "@/store/globalState"
import util from "@/util"

export default class Endings {

  endings: Ending[] = []

  constructor() {
    for (let state of JSON.parse(globalState().endingsString.value)) {
      let ending = new Ending()
      ending.load(state)
      this.endings.push(ending)
    }
  }

  chooseEnding(deck: Deck): Ending {
    let fulfilled = []
    for (let ending of this.endings) {
      if (ending.isFulfilled(deck)) fulfilled.push(ending)
    }
    fulfilled.sort((a, b) => a.likelihood - b.likelihood)
    return fulfilled[0]
  }

  selectEnding(id: string): Ending {
    for (let ending of this.endings) {
      if (ending.id == id) return ending
    }
    console.warn(`Could not select ending with id "${id}".`)
    return this.endings[0]
  }

  correctnessSimulation() {
    let endingsMap: { [id: string]: { fulfilled: number; chosen: number; score: number } } = {}
    for (let ending of this.endings) {
      endingsMap[ending.id] = { fulfilled: 0, chosen: 0, score: 0 }
    }

    // Simulate a bunch of hands
    for (let i = 0; i < 1000; i++) {
      let deck = Deck.referenceDeck().shuffle()
      let drawn = deck.draw(Math.round(3 + Math.random() * (5 * 3 + 8)))
      let picked = new Deck()

      // Add cards to the hand, if the drawn card is a parent, take a random subcard
      for (let card of drawn.cards) {
        if (card.isParent) {
          picked.addCard(util.pickFrom(card.subcards, Math.random()))
        } else {
          picked.addCard(card)
        }
      }

      // Keep stats on each ending
      for (let ending of this.endings) {
        if (ending.isFulfilled(picked)) {
          endingsMap[ending.id].fulfilled += 1
          endingsMap[ending.id].score += ending.intensityScore(picked)
        }
      }
      endingsMap[this.chooseEnding(picked).id].chosen += 1
    }

    for (let ending of this.endings) {
      console.log(ending.title, endingsMap[ending.id])
    }
  }

  testBimboEnding() {
    let ending = this.endings.filter(ending => ending.id == "bimbo")[0]
    let deck = new Deck()
    deck.addCard(Deck.referenceDeck().getCardById("intelligence.intelligence 3")!)
    deck.addCard(Deck.referenceDeck().getCardById("breast-size.breast size 3")!)
    //deck.addCard(Deck.referenceDeck().getCardById("intelligence.breast size intelligence 1")!)

    this.assert(ending.isFulfilled(deck))
  }

  private assert(value: boolean) {
    if (!value) console.error("Assertion failed")
  }

}
