import Game from "@/Game"
import GameMode from "@/GameMode"
import TextStage from "@/stages/TextStage"
import GameButton from "@/GameButton"
import DrawStage from "@/stages/DrawStage"
import GameStage from './stages/GameStage'
import DialogueTree from "@/DialogueTree"
import DialogueStage from "@/stages/DialogueStage"

export default class StoryMode extends GameMode {
  private stage1: GameStage

  constructor(game: Game) {
    super(game)

    this.stage1 = new DialogueStage(this.game)
    let stageD = new DrawStage(this.game)

    this.stage1.endpoints.drawStage = () => this.stage1.nextStage(stageD)
    this.stage1.endpoints.leave = () => this.game.menu.endGame()
    DialogueTree.parseDialogue(this.game.dialogue2, this.stage1).start()
  }

  get firstStage(): GameStage {
    return this.stage1
  }
}
